import React, { useState } from "react";
import AveragePerHour from "./graphs/averagePerHour";

function Averages(props) {
  const [averages] = useState([...props.averages]);
  return (
    <>
      {averages.map((average, index) => {
        return (
          <div
            className={average.type + "-per-hour"}
            key={average.type + index}
          >
            <AveragePerHour
              platform={props.platform}
              type={average.type}
              text={average.text}
            />
          </div>
        );
      })}
    </>
  );
}

export default Averages;
